*{
    margin: 0;
    padding: 0;
}
.banner_img{
    width: 100%;
    height: 40%;
    mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
    margin-top: 0px;

}
.banner>h3{
    color: white;
    position:absolute;
    left:5vw;
    top:57vh;
    font-size: 4vh;
    font-weight: 500;
}
.banner > h3 > small{
    font-weight: lighter;

}
.banner>h1{
    color: white;
    position:absolute;
    left:4.6vw;
    top:57vh;
    font-size: 15vh;
    font-weight: 700;
}

.button_div{
    width: 100%;
}

.button_div>button{
    background-color: rgba(255,255,255,0.109);
    color: white;
    border: 0;
    position: absolute;
    top:75vh;
    min-width:10vw;
    max-width: 15vw;
    padding-bottom:5px;
    padding-top: 5px;
    transition: .2s;
}
.button_div>.button1{
    left:5vw;
}

.button_div>.button2{
    left:15.5vw;
}

.button_div> button:focus{
    outline: none;
    transform: scale(1.05);
}

.intro_container{
    position:absolute;
    left: 5vw;
    top:37vw;
    width:22vw;

}

.intro{
    color: white;
    font-size: 10px;
}



